.App {
  text-align: center;
  overflow-x: hidden;
  min-height: 100vh;
  font-variant: small-caps;
}

.App-logo {
  height: 65vh;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #96A8C8;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

.slideshow {
  display: grid;
  justify-content: space-evenly;
  grid-template-columns: 32% 32% 32%;
  height: 65vh;
}

.slideshow img {
  max-width: 100%;
  max-height: 50vh;
}

.nav-footer nav {
  display: grid;
  justify-content: space-evenly;
  grid-template-columns: 23% 23% 23% 23%;
}

.nav-footer nav button {
  width: 30%;
  margin: 0 auto;
  text-align: center; 
  height: 7vh; 
  font-size: 17px;
  font-weight: bold;
  background-color: #6E7B91;
  color: white;
  cursor: pointer;
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2),0px 2px 2px 0px rgba(0,0,0,0.14),0px 1px 5px 0px rgba(0,0,0,0.12);
  border-radius: 7px;
  border: 0;
  font-variant: small-caps;
}

.credit {
  padding-top: 20px;
  font-size: 17px;
}